.title {
  align-items: center;
  color: #fff;
  fill: #fff;
  display: flex;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 10px;
  padding: 0 5px;
}

.title svg {
  height: 28px;
  margin-right: 10px;
  width: 28px;
}

.todolist {
  flex: 1 1;
  overflow: auto;
}

.listEmpty {
  align-items: center;
  color: hsla(0, 0%, 100%, .8);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.todolist>svg {
  height: 50px;
  width: 50px;
}

.op {
  font-size: 16px;
}

.opHeader {
  border-bottom: 1px solid #ebedf0;
  color: #969799;
  padding: 15px 0;
  text-align: center;
}

.opItem {
  color: #646566;
  padding: 15px 0;
  text-align: center;
}

.space {
  background: #f7f8fa;
  height: 8px;
}

.opItemRemove {
  color: #ee0a24;
}

// 输入框样式

.taskInput {
  align-items: center;
  background: #eeeff3;
  border-radius: 3px;
  color: var(--color-text);
  display: flex;
  margin-bottom: 15px;
  margin-top: 5px;
  padding: 12px 10px;
}

.taskInput svg {
  height: 20px;
  width: 20px;
}

input {
  background: none;
  border: none;
  color: var(--color-text);
  flex: 1 1;
  font-size: 14px;
  margin-left: 10px;
  outline: none;
  overflow: hidden;
  text-overflow: ellipsis;
}