.pwdPage {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.title {
  align-items: center;
  color: #fff;
  fill: #fff;
  display: flex;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 10px;
  padding: 0 5px;
}

.title svg {
  height: 28px;
  margin-right: 10px;
  width: 28px;
}

.formWrap {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 30vh;
}

.container {
  border: 1px solid #fff;
  background-color: #f6f8fa;
  border-radius: 4px;
  padding: 16px;
}

.container button {
  background-color: #788cde;
  border-radius: 4px;
  border: none;
  color: #fff;
  letter-spacing: 6px;
  font-size: 1.2rem;
  padding: 10px;
  width: 100%;
}