.loginPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  color: #fff;
  font-family: Whyte, sans-serif;
  overflow: hidden;
  padding: 0 20px;
  padding-bottom: 15vh;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  align-items: center;
  fill: #fff;
  display: flex;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 40px;
  padding: 0 5px;
  padding-right: 30px;
}

.title svg {
  height: 50px;
  width: 50px;
  margin-right: 15px;
}

.container {
  border: 1px solid #fff;
  background-color: #f6f8fa;
  border-radius: 4px;
  padding: 16px;
  padding-bottom: 0;
}

.container button {
  background-color: #788cde;
  border-radius: 4px;
  border: none;
  color: #fff;
  word-spacing: 2px;
  font-size: 1.2rem;
  padding: 10px;
  width: 100%;
}

.callout {
  margin-top: 16px;
  padding: 16px 16px;
  text-align: center;
  font-size: 14px;
  border-radius: 6px;
  color: var(--color-text);
}

.callout a {
  color: var(--color-primary);
  text-decoration: none;
  cursor: pointer;
}